div.hawaiiInformationsBackground {
  position: relative;
  padding: 10px 0;
  width: 100%;
  min-height: auto;
  background-position: inherit;
  background-size: auto;
  background-repeat: no-repeat;
  background-image: url(./images/skos3.png);
}

img.hawaiiArrow {
  width: 3%;
  margin: 20px auto;
  display: block;
  transform: rotate(180deg);
  content: url("./images/arrow-mobile.png");
}

.hawaiiNutritionalValues {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  color: #CD2D17;
  justify-content: space-around;
}

p.hawaiiTitle {
  font-size: 20px;
  margin-bottom: 5px;
  color: #CD2D17;
}

div.hawaiiLineInformations {
  width: 180px;
  height: 5px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(./images/line.png);
  margin-bottom: 15px;
}
div.hawaiiLineAlergens {
  width: 80px;
  height: 5px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(./images/line.png);
}

div.hawaiiNutritionalValue {
  display: flex;
}

div.hawaiiNutritionalValue p {
  flex-basis: 20%;
  margin: 0;
}

.hawaiiAlergensDescription {
  color: #CD2D17;
  display: none;
  transition: 0.3s;
}

@media (min-width: 768px) {
  div.hawaiiLineInformations {
    width: 310px;
  }
  div.hawaiiLineAlergens {
    width: 140px;
  }
  p.hawaiiTitle {
    font-size: 35px;
  }
  div.hawaiiNutritionalValue,
  .hawaiiAlergensDescription {
    font-size: 18px;
  }
}

@media (min-width: 1024px) {
  img.hawaiiArrow {
    margin: 20px auto;
    content: url("./images/arrow.png");
  }
  div.hawaiiInformationsBackground {
    display: block;
    height: 0;
    background-image: none;
  }

  p.hawaiiTitle {
    font-size: 23px;
    color: #CD2D17;
    font-weight: 700;
  }
  div.hawaiiLineInformations {
    width: 240px;
    height: 12px;
    background-image: url(./images/lineYellow.png);
    background-size: contain;
    background-position: center;
  }

  div.hawaiiLineAlergens {
    width: 120px;
    height: 11px;
    background-image: url(./images/lineYellow.png);
    background-position: center;
  }
  .hawaiiNutritionalValues {
    color: #CD2D17;
    transition: 0.1s;
  }

  div.hawaiiNutritionalValue,
  .hawaiiAlergensDescription {
    font-size: 12px;
  }

  .hawaiiAlergensDescription {
    color: #CD2D17;
    display: block;
  }
}
