div.hawaiiTopBackground {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  height: 74vh;
  min-height: 360px;
  z-index: 3;
  background-image: url(../images/background-top-hawaii.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  margin-bottom: 120%;
}

div.hawaii {
  text-align: center;
  position: absolute;
  left: -2%;
  top: 10%;
  width: 100%;
}
div.hawaii img {
  width: 78%;
}

div.circle-red {
  background-image: url(../../../../common/images/Circle-red.png);
}

p.hawaiiDescription {
  position: absolute;
  top: 100%;
  color: #CE2D17;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  padding: 0px 40px 15px;
  margin-top: 80px;
}

div.hawaiiPizza {
  margin: 0 auto;
  text-align: center;
  width: 90%;
  max-width: 620px;
}

.hawaiiPizzaAnimation {
  margin: 0 auto;
  background-position-y: 0%;
  background-size: 99.8%;
  background-repeat: no-repeat;
  margin-left: auto;
}

@media (min-width: 320px) {
  div.hawaiiTopBackground {
    margin-bottom: 90%;
  }
}

@media (min-width: 360px) {
  p.hawaiiDescription {
    padding: 15px 40px;
  }
}

@media (min-width: 500px) {
  p.hawaiiDescription {
    font-size: 20px;
    margin-right: 5%;
    margin-left: 5%;
    margin-top: 80px;
  }
}
@media (min-width: 550px) {
  p.hawaiiDescription {
    font-size: 26px;
  }
}

@media (min-width: 760px) {
  p.hawaiiDescription {
    font-size: 30px;
  }
}

@media (min-width: 1024px) {
  .hawaiiPizzaAnimation {
    margin: 0;
    height: 100%;
    background-position-y: 0%;
    background-size: 100%;
    background-repeat: no-repeat;
    margin-left: auto;
  }

  div.hawaiiPizza {
    position: absolute;
    height: auto;
    bottom: 13%;
    right: 4%;
    width: 48%;
    max-width: 1080px;
  }

  div.hawaiiTopBackground {
    width: 100%;
    height: 58vw;
    max-height: 1110px;
    background-image: url(../images/skos-desktop.png);
    background-repeat: no-repeat;
    background-size: contain;
    margin-bottom: 0;
  }

  div.hawaii {
    text-align: left;
    top: 125px;
    margin-left: 8%;
  }
  div.hawaii img {
    width: 39%;
  }
  p.hawaiiDescription {
    color: #CE2D17;
    top: 26%;
    left: 0;
    font-size: 15px;
    width: 38%;
  }

  div.ingredients_desktop img.desktop_hawaiiIngredients {
    width: 45%;
    bottom: 4%;
    left: 3%;
  }
}
@media (min-width: 1200px) {
  p.hawaiiDescription {
    font-size: 20px;
  }
}
@media (min-width: 1365px) {
  p.hawaiiDescription {
    font-size: 22px;
  }
}
@media (min-width: 1439px) {
  p.hawaiiDescription {
    font-size: 24px;
  }
}
@media (min-width: 1599px) {
  p.hawaiiDescription {
    font-size: 25px;
  }
}
@media (min-width: 1920px) {
  p.hawaiiDescription {
    font-size: 32px;
  }
}
